<template>
  <div v-if="currentOwner" class="edit-form">
    <h4>Owner</h4>
    <form>
      <div class="form-group">
        <label for="title">Title</label>
        <input type="text" class="form-control" id="name" v-model="currentOwner.name" />
      </div>
      <div class="form-group">
        <label for="description">Age</label>
        <input type="text" class="form-control" id="age" v-model="currentOwner.age" />
      </div>
    </form>
    <button class="badge badge-danger mr-2" @click="deleteOwner">Delete</button>

    <button type="submit" class="badge badge-success" @click="updateOwner">Update</button>
    <p>{{ message }}</p>
  </div>

  <div v-else>
    <br />
    <p>Please click on a Tutorial...</p>
  </div>
</template>
<script>
import OwnerDataService from "../services/OwnerDataService";

export default {
  name: "owner",
  data() {
    return {
      currentOwner: null,
      message: ""
    };
  },
  methods: {
    getOwner(id) {
      OwnerDataService.get(id)
        .then(response => {
          this.currentOwner = response.data;
          console.log(this.currentOwner);
        })
        .catch(e => {
          console.log(e);
        });
    },

    updateOwner() {
        console.log(this.currentOwner)
        OwnerDataService.update(this.currentOwner.id, this.currentOwner)
            .then(response => {
                console.log(response.data);
                this.message = ' The Owner was updated succesfully!';
            })
            .catch(e=> {
                console.log(e);
            });
    },
    deleteOwner() {
      OwnerDataService.delete(this.currentOwner.id)
        .then(response => {
          console.log(response.data);
          this.$router.push({name: "owner"});
        })
        .catch(e => {
          console.log(e);
        });
    }
  },
  mounted() {
    this.message = "";
    this.getOwner(this.$route.params.id);
  }
};
</script>
<style>
.edit-form {
  max-width: 300px;
  margin: auto;
}
</style>